<template>
  <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css">

  <div>
    <button @click="openModal(-1)">新增待辦事項</button>
    <table>
      <tr>
        <th width="40%">待辦事項</th>
        <th width="20%">類別</th>
        <th width="20%">圖片</th>
        <th width="20%">操作</th>
      </tr>
      <tr v-for="(job, index) in paginatedJobs" :key="job.id">
        <td>{{ job.job }}</td>
        <td>{{ job.catg }}</td>
        <td><img :src="job.image" v-if="job.image" alt="圖片"></td>
        <td>
          <button class="icon-button" @click="openModal(index + (currentPage - 1) * pageSize)" title="修改">
            Edit
            <i class="fas fa-edit"></i>
          </button>
          <button class="icon-button" @click="delJob(job.id, index + (currentPage - 1) * pageSize)" title="刪除">
            Delete
            <i class="fas fa-trash-alt"></i>            
          </button>          
        </td>
      </tr>
    </table>
    <div class="pagination">
      <button @click="prevPage" :disabled="currentPage === 1">Prev</button>
      <button v-for="page in totalPages" :key="page" @click="setPage(page)" :class="{ active: currentPage === page }">{{ page }}</button>
      <button @click="nextPage" :disabled="currentPage === totalPages">Next</button>
    </div>
    <div v-if="isModalOpen" class="modal">
      <div class="modal-content">
        <div class="modal-header">
          <h2>{{ editJobIdx == -1 ? '新增待辦事項' : '修改待辦事項' }}</h2>
          <span class="close" @click="closeModal">&times;</span>
        </div>
        <div>
          <input v-model="editJob.job" placeholder="待辦事項">
          <select v-model="editJob.catg">
            <option v-for="catgOpt in catgOptions" :key="catgOpt" :value="catgOpt">{{ catgOpt }}</option>
          </select>
          <input type="file" @change="onFileChange">
          <img :src="editJob.image" v-if="editJob.image" class="preview-img" alt="預覽圖片">
        </div>
        <div class="modal-footer">
          <button v-show="editJobIdx == -1" :disabled="!editJob.job" @click="addJob">新增</button>
          <button v-show="editJobIdx > -1" :disabled="!editJob.job" @click="updateJob">確定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import apiService from './apiService.js';
import axios from 'axios';

const API_BASE_URL = 'http://localhost:3000';

// export default {

  class apiService {
  static getJobs() {
    return axios.get(`${API_BASE_URL}/jobs`);
  }

  static addJob(job) {
    // alert("meow" + job.job + job.catg + job.image);
    return axios.post(`${API_BASE_URL}/jobs`, job);
  }

  static updateJob(id, job) {
    return axios.put(`${API_BASE_URL}/jobs/${id}`, job);
  }

  static deleteJob(id) {
    return axios.delete(`${API_BASE_URL}/jobs/${id}`);
  }
}



class Job {
  constructor(job, catg, image = null) {
    this.job = job;
    this.catg = catg;
    this.image = image;
  }
}

export default {
  data() {
    return {
      editJob: new Job('', '工作'),
      editJobIdx: -1,
      isModalOpen: false,
      currentPage: 1,
      pageSize: 4,
      catgOptions: ['工作', '家庭', '休閒'],
      jobs: [],
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.jobs.length / this.pageSize);
    },
    paginatedJobs() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.jobs.slice(start, end);
    },
  },
  methods: {
    fetchJobs() {
      apiService.getJobs()
        .then((response) => {
          this.jobs = response.data;
          if (this.currentPage > this.totalPages) {
            this.currentPage = this.totalPages;
          }
        })
        .catch((error) => {
          console.error('Error fetching jobs:', error);
        });
    },
    reset() {
      this.editJob = new Job('', '工作');
      this.editJobIdx = -1;
      this.isModalOpen = false;
    },
    addJob() {
      apiService.addJob(this.editJob)
        .then((response) => {
          this.jobs.push(response.data);
          this.isModalOpen = false;
          this.setPage(this.totalPages);
        })
        .catch((error) => {
          console.error('Error adding job:', error);
        });
    },
    updateJob() {
      apiService.updateJob(this.editJob.id, this.editJob)
        .then((response) => {
          this.jobs.splice(this.editJobIdx, 1, response.data);
          this.reset();
          if (this.currentPage > this.totalPages) {
            this.currentPage = this.totalPages;
          }
        })
        .catch((error) => {
          console.error('Error updating job:', error);
        });
    },
    delJob(id, index) {
      apiService.deleteJob(id)
        .then(() => {
          this.jobs.splice(index, 1);
          if (this.currentPage > this.totalPages) {
            this.currentPage = this.totalPages;
          }
        })
        .catch((error) => {
          console.error('Error deleting job:', error);
        });
    },
    openModal(index) {
      if (index === -1) {
        this.editJob = new Job('', '工作');
      } else {
        this.editJob = { ...this.jobs[index] };
      }
      this.editJobIdx = index;
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
    onFileChange(e) {
      const file = e.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.editJob.image = event.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    setPage(page) {
      this.currentPage = page;
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
  },
  mounted() {
    this.fetchJobs();
  },
};
</script>

<style scoped>
html,
body {
  font-size: 10pt;
}

table {
  font-size: 9pt;
  width: 100%;
  border-collapse: collapse;
  margin-top: 12px;
}

td,
th {
  border: 1px solid gray;
  padding: 2px;
  text-align: center;
}

td:first-child {
  text-align: left;
}

th {
  background-color: #f0f0f0;
}

pre {
  background-color: #eee;
  font-size: 9pt;
  margin-top: 12px;
  padding: 12px;
  width: 100%;
}

img {
  max-width: 50px;
  max-height: 50px;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
}

.icon-button i {
  font-size: 16px;
}


.icon {
  cursor: pointer;
  margin-right: 5px;
}

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 300px;
  max-width: 80%;
}

.preview-img {
  max-width: 300px;
  max-height: 300px;
  margin-top: 10px;
}

.modal-header,
.modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h2 {
  margin: 0;
}

.close {
  cursor: pointer;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
}
</style>
