<template>
  <div>
    <div id="toolbar-container">
      <div id="toolbar">

         <!-- Basic formatting buttons -->
          <button class="ql-bold">Bold</button>
          <button class="ql-italic">Italic</button>
          <button class="ql-underline">Underline</button>
          <button class="ql-strike">Strike</button> <!-- Added strike-through -->

        <button class="ql-color">Color</button>
        <button class="ql-background">Background</button>

          <!-- Text headers -->
          <button class="ql-header" value="1">H1</button>
          <button class="ql-header" value="2">H2</button>
          <button class="ql-header" value="3">H3</button> <!-- Added H3 -->

          <!-- List formatting buttons -->
          <button class="ql-list" value="ordered">List</button>
          <button class="ql-list" value="bullet">Bullet</button>

          <!-- Text alignment options -->
          <button class="ql-align" value="">Left</button>
          <button class="ql-align" value="center">Center</button>
          <button class="ql-align" value="right">Right</button>
          <button class="ql-align" value="justify">Justify</button> <!-- Added justify alignment -->

          <!-- Additional formatting buttons -->
          <button class="ql-link">Link</button>
          <button class="ql-image">Image</button>
          <button class="ql-video">Video</button> <!-- Added video embedding -->
          <button class="ql-code-block">Code</button>
          <button class="ql-blockquote">Quote</button>
          <button class="ql-clean">Clear</button>

          <!-- Added dropdowns for font family and size -->

        
        <button @click="attachFile" class="attachment-button">
          <i class="fas fa-paperclip"></i>
        </button>
      </div>
    </div>
    <div ref="editorContainer" id="editor-container"></div>
    <div id="attachment-container" class="container">
      附件: 
      <div v-for="file in attachments" :key="file.name">
        <a :href="file.data" :download="file.name"> {{ file.name }}</a>
      </div>
    </div>

    <div v-if="debug">
    
    <button @click="saveContent">Save Content</button>
    <button @click="loadContent">Load Content</button>
    <p>Saved Content</p>
    <div id="save-container" class="container">{{ savedContent }}</div>
    Loaded Content    
    <div id="display-container" class="container" ref="displayContainer"></div>
    <div id="loaded-attachment-container" class="container">
      <!-- Container for displaying loaded attachments -->
      <div v-for="file in loadedAttachments" :key="file.name">
        Loaded attachment: {{ file.name }} - <a :href="file.data" :download="file.name">Download</a>
      </div>
    </div>

    </div>
    
  </div>
</template>

<script>
import Quill from 'quill';
import 'quill/dist/quill.snow.css';

export default {
  name: 'EmailEditor',

  
  data() {
    return {
      quill: null,
      attachments: [],
      loadedAttachments: [], // Added for handling loaded attachments separately
      savedContent: '',
      loadedContent: '',
      debug:false
    };
  },
  mounted() {
    this.quill = new Quill(this.$refs.editorContainer, {
      theme: 'snow',
      modules: {
        toolbar: '#toolbar'
      }
    });

    if( this.debug ){

    this.readOnlyQuill = new Quill(this.$refs.displayContainer, {
      theme: 'snow',
      readOnly: true,  // Make this Quill instance read-only
      modules: {
        toolbar: false  // Disable toolbar for read-only instance
      }
    });
    }
  },
  methods: {

    set_doc(newDoc) {
      if (this.quill) {
        this.quill.root.innerHTML = newDoc.content;
      }
      this.attachments = newDoc.attachments;
    },
    get_doc() {
      // alert("mi");
      return {
        content: this.quill ? this.quill.root.innerHTML : this.doc.content,
        attachments: this.attachments
      };
    },   
    attachFile() {
      const input = document.createElement('input');
      input.type = 'file';
      input.click();
      input.onchange = () => {
        const file = input.files[0];
        const reader = new FileReader();
        reader.onloadend = () => {
          this.attachments.push({ name: file.name, data: reader.result });
        };
        reader.readAsDataURL(file);
      };
    },
    getContent() {
      // Assuming `quill` is the Quill editor instance
      return this.quill.root.innerHTML;
    },
    saveContent() {
      const editorContent = this.quill.root.innerHTML;
      this.savedContent = JSON.stringify({
        content: editorContent,
        attachments: this.attachments
      });
    },
    loadContent() {
      const data = JSON.parse(this.savedContent || '{}');
      this.loadedContent = data.content || 'No content loaded';
      this.readOnlyQuill.root.innerHTML = this.loadedContent;
      
      this.loadedAttachments = data.attachments || [];
    }
  }
};
</script>

