.attachment-button i {
  margin-right: 5px; /* Adds spacing between the icon and text */
}
.fas.fa-paperclip {
  color: #333; /* Sets the color of the clip icon */
  font-size: 16px; /* Sets the size of the clip icon */
}
</style>
