<template>
  <div>
    <button @click="openNewModal">Add New</button>
    <table>
      <thead>
        <tr>
          <th v-for="(header, index) in headers" :key="index">{{ header }}</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(row, index) in paginatedData" :key="index">
          <td>{{ row.id }}</td>
          <td>{{ row.type }}</td>
          <td>{{ row.title }}</td>
          <td>{{ row.lastUpdatedTime }}</td>
          <!---
          <td>{{ row.content }}</td>
          --->
          <td>
            <button @click="openEditModal(row)">Edit</button>
            <button @click="deleteRow(row.id)">Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
    <TablePagination
      :total="totalItems"
      :current="currentPage"
      :perPage="itemsPerPage"
      :visiblePageCount="5"
      @page-changed="pageChanged"
    />
    <EditModal
      v-if="showEditModal"
      :record="currentRecord"
      @close="closeEditModal"
      @save="saveRecord"
    />
    <EditModal
      v-if="showNewModal"
      :record="newRecord"
      @close="closeNewModal"
      @save="addRecord"
      :isNew="true"
    />
    <!-- <div class="data-section">
      <h3>All Data Content</h3>
      <ul>
        <li v-for="(row, index) in data" :key="index">
          {{ row }}
        </li>
      </ul>
    </div> -->
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import TablePagination from './TablePagination.vue';
import EditModal from './EditModal.vue';
import DataDAO from '../data/dataDAO.js';

export default {
  components: { TablePagination, EditModal },
  setup() {
    const headers = ref(["ID", "Type", "Title", "Last Updated Time"]);
    const dao = new DataDAO();
    const data = ref(dao.getAll());
    const totalItems = ref(data.value.length);
    const currentPage = ref(1);
    const itemsPerPage = ref(5);

    const paginatedData = computed(() => {
      const start = (currentPage.value - 1) * itemsPerPage.value;
      const end = start + itemsPerPage.value;
      return data.value.slice(start, end);
    });

    const pageChanged = (page) => {
      currentPage.value = page;
    };

    const showEditModal = ref(false);
    const showNewModal = ref(false);
    const currentRecord = ref(null);
    const newRecord = ref({ id: null, type: "", title: "", lastUpdatedTime: "", content: "" });

    // const openEditModal = (record) => {
    //   currentRecord.value = { ...record };
    //   showEditModal.value = true;
    // };

    const openEditModal = (record) => {
      // Use structured cloning to create a deep copy of the record for editing
      currentRecord.value = JSON.parse(JSON.stringify(record));
      showEditModal.value = true;
    };

    const openNewModal = () => {
      newRecord.value = { id: null, type: "", title: "", lastUpdatedTime: "", content: {"content": "", "attachments": []} };
      showNewModal.value = true;
    };

    const closeEditModal = () => {
      showEditModal.value = false;
      currentRecord.value = null;
    };

    const closeNewModal = () => {
      showNewModal.value = false;
      newRecord.value = { id: null, type: "", title: "", lastUpdatedTime: "", content: "" };
    };

    const saveRecord = (updatedRecord) => {

      // alert(updatedRecord.value);
      dao.update(updatedRecord);
      data.value = [...dao.getAll()];  // Create a new array to ensure reactivity
      totalItems.value = data.value.length;
      closeEditModal();
    };

    const addRecord = (newRecord) => {
      dao.add(newRecord);
      data.value = [...dao.getAll()];  // Create a new array to ensure reactivity
      totalItems.value = data.value.length;
      closeNewModal();
      currentPage.value = Math.ceil(totalItems.value / itemsPerPage.value);
      pageChanged(currentPage.value);
    };

    const deleteRow = (id) => {
      dao.delete(id);
      data.value = [...dao.getAll()];  // Create a new array to ensure reactivity
      totalItems.value = data.value.length;
      const totalPages = Math.ceil(totalItems.value / itemsPerPage.value);
      if (currentPage.value > totalPages) {
        currentPage.value = totalPages;
      }
      pageChanged(currentPage.value);
    };

    return {
      headers,
      paginatedData,
      totalItems,
      currentPage,
      itemsPerPage,
      pageChanged,
      showEditModal,
      showNewModal,
      currentRecord,
      newRecord,
      openEditModal,
      openNewModal,
      closeEditModal,
      closeNewModal,
      saveRecord,
      addRecord,
      deleteRow,
      data,
    };
  },
};
</script>

