<template>
  <div class="modal" v-if="record">
    <div class="modal-content">
      <span class="close" @click="close">&times;</span>
      <h2>{{ isNew ? 'Add New Record' : 'Edit Record' }}</h2>
        <label for="type">Type</label>
        <input type="text" id="type" v-model="editableRecord.type" required>
        <br>
        <label for="title">Title</label>
        <input type="text" id="title" v-model="editableRecord.title" required>
        <br>
        <label for="lastUpdatedTime">Last Updated Time</label>
        <input type="datetime-local" id="lastUpdatedTime" v-model="editableRecord.lastUpdatedTime" disabled>
        <br>
        <label for="content">Content</label>
        <email-editor ref="docEditor"></email-editor>          
        <button type="button" @click="save">{{ isNew ? 'Add' : 'Save' }}</button>


    </div>
  </div>
</template>

<script>
  // import { ref, watch, onMounted, nextTick } from 'vue';
  import EmailEditor from './EmailEditor.vue';

  export default {
    components: {
      EmailEditor
    },
    props: {
      record: {
        type: Object,
        required: true,
      },
      isNew: {
        type: Boolean,
        default: false,
      },
    },
    data() {
      return {
        editableRecord: { ...this.record }
      };
    },
    emits: ['close', 'save'],
    methods: {
      close() {
        this.$emit('close');
      },
      save() {
        // alert("meow");
        if (this.$refs.docEditor) {
          const docc = this.$refs.docEditor.get_doc();
          if (docc) {
            // alert(docc.content);
            // alert(docc.attachments);
            this.editableRecord.content = docc;
            // alert(JSON.stringify(docc));
          }
          this.editableRecord.lastUpdatedTime = new Date().toISOString().slice(0, 16);
          this.$emit('save', { ...this.editableRecord });
        }
      },
      handleFormSubmit(event) {
        event.preventDefault();
        // this.save();
      },
      setInitialDoc() {
        const initialDoc = this.editableRecord.content;
        // const initialDoc = {
        //   content: '<p>Initial content for the editor.</p>',
        //   attachments: []  // Assuming attachments are part of the doc
        // };
        if (this.$refs.docEditor) {
          this.$refs.docEditor.set_doc(initialDoc);
        } else {
          console.error("EmailEditor is not available at this moment.");
        }
      }
    },
    watch: {
      record(newRecord) {
        this.editableRecord = { ...newRecord };
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.setInitialDoc();
      });
    }
  };
  </script>


