<template>
  <div class="pagination">
    <div class="buttons">
      <button
        @click="prevNPages"
        :disabled="current <= 1"
      >
        《《
      </button>
      <button
        @click="prevPage"
        :disabled="current === 1"
      >
        &laquo; <!-- Left arrow -->
      </button>
      <button
        v-for="page in visiblePages"
        :key="page"
        @click="changePage(page)"
        :class="{ active: current === page }"
      >
        {{ page }}
      </button>
      <button
        @click="nextPage"
        :disabled="current === totalPages"
      >
        &raquo; <!-- Right arrow -->
      </button>
      <button
        @click="nextNPages"
        :disabled="current > totalPages -1 "
      >
        》》
      </button>
    </div>
    <span class="current-page-label">
      Page {{ current }} of {{ totalPages }}
    </span>
  </div>
</template>

<script>
export default {
  props: {
    total: {
      type: Number,
      required: true,
    },
    current: {
      type: Number,
      required: true,
    },
    perPage: {
      type: Number,
      required: true,
    },
    visiblePageCount: {
      type: Number,
      required: false,
      default: 5, // default value if not provided
    },
  },
  computed: {
    totalPages() {
      return Math.ceil(this.total / this.perPage);
    },
    visiblePages() {
      const pages = [];
      const range = Math.floor(this.visiblePageCount / 2);
      let start = Math.max(1, this.current - range);
      let end = Math.min(this.totalPages, this.current + range);

      // Adjust start and end if the number of visible pages is less than the maximum
      if (end - start + 1 < this.visiblePageCount) {
        if (start === 1) {
          end = Math.min(this.totalPages, start + this.visiblePageCount - 1);
        } else if (end === this.totalPages) {
          start = Math.max(1, end - this.visiblePageCount + 1);
        }
      }

      for (let i = start; i <= end; i++) {
        pages.push(i);
      }

      return pages;
    },
  },
  methods: {
    prevPage() {
      if (this.current > 1) {
        this.$emit('page-changed', this.current - 1);
      }
    },
    nextPage() {
      if (this.current < this.totalPages) {
        this.$emit('page-changed', this.current + 1);
      }
    },
    prevNPages() {
      const newPage = Math.max(1, this.current - this.visiblePageCount);
      this.$emit('page-changed', newPage);
    },
    nextNPages() {
      const newPage = Math.min(this.totalPages, this.current + this.visiblePageCount);
      this.$emit('page-changed', newPage);
    },
    changePage(page) {
      this.$emit('page-changed', page);
    },
  },
};
</script>

<style>
.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}
button {
  margin: 0 5px;
  padding: 5px 10px;
  border: none;
  background-color: #f2f2f2;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
button.active {
  font-weight: bold;
  background-color: #ddd;
  animation: shine 1.5s infinite;
}
button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.current-page-label {
  margin-top: 10px;
  font-weight: bold;
}
@keyframes shine {
  0% {
    box-shadow: 0 0 10px #ffcc00;
  }
  50% {
    box-shadow: 0 0 20px #ffcc00;
  }
  100% {
    box-shadow: 0 0 10px #ffcc00;
  }
}
</style>
